<template>
  <md-card
    class="betslip md-elevation-24"
    :class="{ 'open': isOpen }"
    v-touch:swipe.top="swipeHandler"
    v-touch:swipe.bottom="swipeHandler"
    v-on:click.prevent
  >
    <hr class="divider" />
    <div class="md-layout action-buttons">
      <md-button class="md-layout-item md-raised betslip-btn">Betslip {{ noInBetslip }}</md-button>
      <md-button class="md-layout-item md-raised md-primary betslip-btn">€50.00</md-button>
      <md-button class="md-layout-item md-raised betslip-btn">Bets {{ noOfBets }}</md-button>
    </div>
    <flicking
      class="cards-holder"
      v-for="suggestion in suggestions" :key="suggestion"
      :options="{
        moveType: 'snap',
        duration: 200,
        gap: 32,
        autoResize: true,
        adaptive: true,
        overflow: true,
        isEqualSize: true,
        isConstantSize: true,
        collectStatistics: false
      }"
    >
      <div><Card :nr-of-runners="3" section="Betslip-1" :cardIndex="suggestion" v-on:btn-click="onCardButtonClick" :buttons-selected="buttonsSelected"></Card></div>
      <div><Card :nr-of-runners="3" section="Betslip-2" :cardIndex="suggestion" v-on:btn-click="onCardButtonClick" :buttons-selected="buttonsSelected"></Card></div>
      <div><Card :nr-of-runners="3" section="Betslip-3" :cardIndex="suggestion" v-on:btn-click="onCardButtonClick" :buttons-selected="buttonsSelected"></Card></div>
    </flicking>
    <div class="place-bets-btn md-layout">
      <md-button class="md-raised md-layout-item md-size-100 betslip-btn">Place Bet</md-button>
    </div>
  </md-card>
</template>

<script>
import Card from './Card';
import { Flicking } from '@egjs/vue-flicking';

export default {
  name: "Betslip",
  components: {
    Card,
    Flicking
  },
  props: {
    noInBetslip: Number,
    noOfBets: Number,
    buttonsSelected: Array
  },
  data() {
    return {
      suggestions: [1, 2],
      isOpen: false
    };
  },
  methods: {
    swipeHandler(direction) {
      this.isOpen = direction === 'top' ? true : false;
      this.$emit('betslipStatusUpdate', this.isOpen);
    },
    onCardButtonClick(btnId) {
      this.$emit('btn-click', btnId);
    }
  }
};
</script>

<style lang="scss" scoped>
.betslip {
  border-radius: 16px 16px 0 0;
  background-color: white;
  text-align: center;
  width: 100%;
  height: 100vh;
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 2001;
  transition: transform 0.8s;
  transform: translateY(88vh);

  &.open {
    transform: translateY(20vh);
  }
}

.divider {
  border: 1px solid #004833;
  margin: 10px 40% 5px 40%;
}

.betslip-btn {
  border-radius: 12px;
}

.cards-holder {
  margin: 10px 0;
}

.action-buttons {
  margin: 0 5px;
}

.place-bets-btn {
  margin: 12px;
}
</style>