<template>
  <div id="app" :class="{ 'app-no-scroll': lockScroll }">
    <flicking
      :options="{
        moveType: 'snap',
        duration: 200,
        collectStatistics: false,
        autoResize: true,
        adaptive: true,
        isEqualSize: false
      }"
    >
      <SportPage sport-name="Football" :items="footballItems" v-on:btn-click="updateSelectedButtons" :buttons-selected="buttonsSelected"></SportPage>
      <SportPage sport-name="Horse-Racing" :items="horseRacingItems" v-on:btn-click="updateSelectedButtons" :buttons-selected="buttonsSelected"></SportPage>
      <SportPage sport-name="Tennis" :items="tennisItems" v-on:btn-click="updateSelectedButtons" :buttons-selected="buttonsSelected"></SportPage>
    </flicking>
    <Betslip v-on:betslipStatusUpdate="updateScrollLock" :no-of-bets="noOfBets" :no-in-betslip="noInBetslip" v-on:btn-click="updateSelectedButtons" :buttons-selected="buttonsSelected"></Betslip>
  </div>
</template>

<script>
import SportPage from './components/SportPage';
import Betslip from './components/Betslip';
import { Flicking } from '@egjs/vue-flicking';

export default {
  name: "App",
  data() {
    return {
      buttonsSelected: [],
      noOfBets: 0,
      lockScroll: false,
      footballItems: [5, 2, 3, 4, 5, 2, 3],
      horseRacingItems: [2, 4, 5, 2, 3],
      tennisItems: [5, 4, 1, 5, 4]
    }
  },
  computed: {
    noInBetslip() {
      return this.buttonsSelected.length;
    }
  },
  methods: {
    updateScrollLock(status) {
      this.lockScroll = status;
    },
    updateSelectedButtons(btnId) {
      const alreadyExists = this.buttonsSelected.some(item => item === btnId);

      if (!alreadyExists) {
        return this.buttonsSelected.push(btnId);
      }

      this.buttonsSelected = this.buttonsSelected.filter(item => item !== btnId);
    }
  },
  components: {
    SportPage,
    Flicking,
    Betslip
  }
};
</script>

<style>
html,body {
    -webkit-overflow-scrolling: touch;
}

body {
  padding-bottom: 12vh;
  overscroll-behavior-y: contain;
  -webkit-user-select: none;
  -webkit-touch-callout: none;
}

.app-no-scroll {
  position: fixed;
  width: 100%;
}
</style>