<template>
  <md-card class="card">
    <md-list>
      <md-list-item v-for="(runner, index) in listOfRunners" :key="index">
        <div class="runners">Runner A / Runner B</div>
        <md-button class="md-raised odds" :class="{ 'md-primary': isButtonSelected(index, '1') }" @click="onBtnClick(index, '1')">2/1</md-button>
        <md-button class="md-raised odds" :class="{ 'md-primary': isButtonSelected(index, '2') }" @click="onBtnClick(index, '2')">5/4</md-button>
      </md-list-item>
    </md-list>
  </md-card>
</template>

<script>
export default {
  name: "Card",
  props: {
    section: String,
    cardIndex: Number,
    nrOfRunners: Number,
    buttonsSelected: Array
  },
  computed: {
    listOfRunners() {
      return Array.from([...Array(this.nrOfRunners)]);
    }
  },
  methods: {
    onBtnClick(runnerIndex, btnIndex) {
      this.$emit('btn-click', `${this.section}-${this.cardIndex}-${runnerIndex}-${btnIndex}`);
    },
    isButtonSelected(runnerIndex, btnIndex) {
      return this.buttonsSelected && this.buttonsSelected.some(item => item === `${this.section}-${this.cardIndex}-${runnerIndex}-${btnIndex}`);
    }
  }
};
</script>

<style lang="scss" scoped>
.card {
  border-radius: 8px;
  background-color: white;
}
.runners {
  font-size: 12px;
}

.odds {
  border-radius: 4px;
}

.selected {
  background-color: pink;
}
</style>